
<template>
  <div class="update">
    <vx-header :showback="1" :title="$t('settings.checkForUpdates')"></vx-header>
    <div class="vx-container">
      <div class="logo"></div>
      <div class="br">{{$t('common.version')}} {{version}}</div>
      <div class="list" v-if="hasLoaded<100">{{$t('settings.versiond')}}</div>

      <div class="more">
        <vx-cell :title="$t('settings.more')" @link="$router.push({name:'verdetail'})"></vx-cell>
      </div>

      <div class="down" v-show="status===2">
        <div class="loading-circle">
          <p class="text"><span id="loadedNum">0</span><span class="bfh">%</span></p>
          <canvas class="mask" id="loadingProgress" width="110" height="110"></canvas>
          <canvas class="bg" id="loadingProgressBg" width="110" height="110"></canvas>
        </div>

        <div class="detail">
          {{$t('common.currentVer')}}
        </div>

      </div>
      <div v-if="status===2" class="download yellow-btn" @click="download">{{$t('common.updating')}}</div>
      <div v-if="status===0" class="download yellow-btn" @click="download">{{$t('common.downloading')}}</div>
      <div v-if="status===3" class="download yellow-btn" @click="install">{{$t('common.install')}}</div>
    </div>
  </div>
</template>

<script>
  window.ulp = null;
  export default {
    data() {
      return {
        version: 'V2.0.0.S2T2',
        hasLoaded: 0,//下载进度
        hasUpdate: false,//是否有新版本
        percentage: 0,//上一次的下载进度
        status: 1//1没有新版本，0有新版本，2下载中，3下载完成
      };
    },
    created() {
      this.version = window.Android.getVersionName();
      this.status = window.Android.checkVersion();

    },
    mounted() {
      window.ulp = this.ulp;
      this.drawBg();
    },
    methods: {
      download() {
        this.status = 2;
        if(!window.Android.startUpdate()) {
          this.status = 0;
        }
      },
      install() {
        window.Android.installApp();
      },
      ulp(percent) {
        if(percent === 100) {
          setTimeout(() => {
            this.status = 3;
          }, 1000);
        }

        this.percentage = this.hasLoaded;
        let vm = this;
        let i = 0;
        let draw = null;
        draw = setInterval(function() {
          if(vm.hasLoaded > 100) {
            clearInterval(draw);
            draw = null;
            return true;
          }
          if(i < percent - vm.percentage) {
            i++;
            vm.hasLoaded += 1;
            vm.d();
          } else {
            clearInterval(draw);
            draw = null;
          }
        }, 10);
      },
      d() {
        let canvas = document.getElementById('loadingProgress');
        let ctx = canvas.getContext('2d');
        var lp = document.getElementById('loadedNum');
        lp.innerHTML = this.hasLoaded;

        let loaded = this.hasLoaded * 2 / 100 * Math.PI;
        const cw = canvas.width;
        const hcw = canvas.width / 2;
        ctx.clearRect(0, 0, cw, cw);
        ctx.beginPath();
        ctx.lineCap = 'round';
        ctx.arc(hcw, hcw, hcw - 5, 0, loaded, false);
        ctx.lineWidth = 8;
        ctx.strokeStyle = '#007aff';
        ctx.stroke();
      },
      drawBg() {
        window.canvas = document.getElementById('loadingProgressBg');
        const cw = canvas.width;
        const hcw = canvas.width / 2;
        let ctx = canvas.getContext('2d');
        ctx.beginPath();
        ctx.arc(hcw, hcw, hcw - 5, 0, Math.PI * 2, false);
        ctx.lineWidth = 4;
        ctx.strokeStyle = '#6e6e6e';
        ctx.stroke();
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-container {
    padding: 20px;
  }
  .vx-container {
    background-color: #383940;
  }
  .logo {
    margin-top: 35px;
    height: 160px;
    width: 100%;
    background: ~"url('../../assets/common/vcan.png') no-repeat" #383940;
    background-position: center center;
    background-size: 160px 160px;
  }
  .br {
    font-size: 30px;
    color: #ddd;
    padding-top: 15px;
    text-align: center;
  }
  .detail {
    font-size: 28px;
    color: #ddd;
    padding: 45px 30px;
    text-align: left;
    line-height: 40px;
  }
  .bt {
    border-top: 2px solid #626262;
  }
  .list {
    margin-top: 30px;
    padding: 0 30px;
    line-height: 40px;
    font-size: 28px;
    color: #6e6e6e;
    font-weight: bold;
    text-align: left;
    border-bottom: 2px solid #626262;
  }
  .more {
    border-bottom: 2px solid #626262;
  }
  .bfh {
    font-size: 30px;
  }
  .detail {
    font-size: 26px;
    color: #ddd;
    text-align: center;
    font-weight: bold;
  }
  .loading-circle {
    position: relative;
    width: 220px;
    height: 220px;
    margin: 100px auto 0;
  }
  .loading-circle > * {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    box-sizing: border-box;
  }
  .loading-circle > canvas.bg {
    z-index: 1;
  }
  .loading-circle > canvas.mask {
    z-index: 2;
    transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
  }
  .loading-circle > p {
    z-index: 3;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 220px;
    font-size: 60px;
    font-weight: lighter;
    color: #f8f8f8;
    margin: 0;
  }
  .download {
    width: 590px;
    height: 70px;
    line-height: 70px;
    margin: 50px auto 0;
    color: #ddd;
    background-color: #ab5c15;
    border-radius: 5px;
    font-size: 28px;
  }
</style>




